<template>
  <div class="invoice-detail-container">
    <div class="pay-success">
      <div class="card-wrap">
        <div class="mb-header">
          <p>訂單詳情</p>
        </div>
        <div class="card-detail">
          <div class="package-list">
            <div class="list-item">
              <p>訂單號</p>
              <p>{{invoiceInfo.invoiceid}}</p>
            </div>
            <div class="list-item">
              <p>套餐</p>
              <p>{{invoiceInfo.description}}</p>
            </div>
            <div class="list-item">
              <p>總額</p>
              <p>{{invoiceInfo.balance}} USD</p>
            </div>
            <div class="list-item">
              <p>付款方法</p>
              <p>{{paymentMethod}}</p>
            </div>
            <div class="list-item">
              <p>訂購日期</p>
              <p>{{invoiceInfo.duedate}}</p>
            </div>
            <div class="list-item">
              <p>訂單狀態</p>
              <p>{{invoiceInfo.status}}</p>
            </div>
          </div>
        </div>
        <a-button type="primary" :loading="loading" size="large" v-on:click="backToInvoices">
          {{statusTip}}
        </a-button>
      </div>
    </div>
  </div>
</template>

<script>
  import i18n from '../i18n';

  export default {
    name: 'invoices-detail',
    data() {
      return {
        statusTip: '狀態更新中...',
        loading: true,
        invoiceId: '',
        invoiceInfo: {
          invoiceid: '',
          description: '',
          balance: '',
          paymentMethod: '',
          duedate: '',
          status: '',
          paidDate: '',
          serviceId: '',
          datepaid: ''
        },
        paymentMethod: '',
        payMethodList: [
          {
            id: 0,
            name: '支付寶',
            logo: ['alipay'],
            type: 'payssionalipaycn'
          },
          {
            id: 1,
            name: '信用卡',
            logo: ['visa', 'master', 'unionpay', 'amex'],
            type: 'stripe3dsecure'
          },
          {
            id: 2,
            name: 'PayPal',
            logo: ['paypal'],
            type: 'paypalcheckout'
          },
          {
            id: 3,
            name: '加密貨幣',
            logo: ['btc', 'btc2', 'eth', 'eth2'],
            type: 'coinpayments'
          }
        ],
        loopTimer: Object,
        inapp: false,
        isSentData: false,
        isAndroid: false
      };
    },
    created() {
      let invoicePath = window.location.pathname.match(/invoices\/(.*)/);
      this.inapp = localStorage.getItem('inapp') === 'android';
      this.isAndroid = !!(navigator.userAgent.toLowerCase()
        .indexOf('android') > 1);

      if (invoicePath && invoicePath.length > 1) {
        this.invoiceId = window.location.pathname.match(/invoices\/(.*)/)[1];
        this.loopTimer = setInterval(this.getData, 4000);
      } else {
        // update UI
        this.loading = false;
        this.statusTip = '訂單未找到！';
      }
    },
    methods: {
      getData() {
        // Get the invoice detail
        const that = this
        this.$request.getUsersInvoiceDetail(this.invoiceId, function (data) {
          if (data && data.status === 200) {
            // get the invoice info
            that.invoiceInfo.invoiceid = data.data.invoiceid;
            that.invoiceInfo.description = data.data.items.item[0].description;
            that.invoiceInfo.balance = data.data.total;
            that.invoiceInfo.paymentMethod = data.data.paymentmethod;
            that.invoiceInfo.duedate = data.data.duedate;
            that.invoiceInfo.status = i18n.t(data.data.status === 'Paid' ? 'Paid' : 'Unpaid');
            that.invoiceInfo.paidDate = data.data.paidDate;
            that.invoiceInfo.serviceId = data.data.items.item[0].relid;
            that.invoiceInfo.datepaid = data.data.datepaid
            // get the payment method name
            that.paymentMethod = that.payMethodList.find(value => {
              return value.type === data.data.paymentmethod;
            }).name;

            if (data.data.status === 'Paid') {
              // If user has already paid, then need to get the service for getting the pid and billingcycle
              that.$http.get(`${process.env.VUE_APP_SERVER_URL}/api/v1/services`).then(data => {
                if (data && data.status === 200) {
                  data.data.products.forEach((item, index) => {
                    if (item.id === that.invoiceInfo.serviceId) {
                      // Stop the timer loop
                      window.clearInterval(that.loopTimer);
                      // Update UI
                      that.loading = false;
                      that.statusTip = '完成！';

                      // Send the data to app
                      if (!that.isSentData) {
                        that.isSentData = true
                        if (that.inapp) {
                          // inApp android
                          let eventData = {
                            PID: item.productId || '00000',
                            OID: that.invoiceInfo.invoiceid,
                            INTERVAL: item.billingcycle || 'no interval',
                            PRICE: that.invoiceInfo.balance,
                            CURRENCY: 'USD',
                            QUANTITY: 1,
                            DEVICES: 2,
                            BANDWIDTH: 0,
                            PLACE_ORDER_TIME: that.invoiceInfo.duedate,
                            CHECKOUT_TIME: that.invoiceInfo.datepaid,
                            CHECKOUT_OPTION: that.paymentMethod
                          }
                          that.firebaselogger.logEvent(that.firebaselogger.eventConfig.ECOMMERCE_PURCHASE, eventData);
                        } else {
                          // web GTM
                          window.dataLayer.push({'event': 'purchase',
                            'OID': that.invoiceInfo.invoiceid,
                            'INTERVAL': item.billingcycle || 'no interval',
                            'PRICE': that.invoiceInfo.balance,
                            'CURRENCY': 'USD',
                            'QUANTITY': 1,
                            'DEVICES': 2,
                            'BANDWIDTH': 0,
                            'PLACE_ORDER_TIME': that.invoiceInfo.duedate,
                            'CHECKOUT_TIME': that.invoiceInfo.datepaid,
                            'CHECKOUT_OPTION': that.paymentMethod
                          })
                        }
                      } else if (that.isAndroid) {
                        // back to the app (just for outside browser)
                        setTimeout(function () {
                          let a = document.createElement('a');
                          a.target = '_blank';
                          a.href = `whaleblue://test.com?data=${that.invoiceInfo.toString()}`;
                          a.click();
                        }, 2000);
                      }
                    }
                  })
                }
              })

            }
          } else {
            window.clearInterval(this.loopTimer);
            // update UI
            this.loading = false;
            this.statusTip = '訂單未找到！data && data.status === 200';
          }
        })

          // .catch(reason => {
          //   window.clearInterval(this.loopTimer);
          //   // update UI
          //   this.loading = false;
          //   this.statusTip = '訂單未找到！catch';
          // });
      },
      backToInvoices() {
        window.clearInterval(this.loopTimer);
        this.$router.push({
          path: `/my/invoices`,
          name: 'invoice',
        });
      }
    }
  };
</script>

